    <div  class="mainContent">

      <div class="mainTitle-text">全球服务设计共创节<a style="color: #FF5783">导师助理</a></div>
      <div class="practiceTitle">
        <b  > 职责：</b>协助导师筹备活动，参与共创，并记录拍摄视频照片，后期剪辑，制作推文，在十里芳菲公众号发表。

      </div>
      <img class="imagePractice" src="../assets/practice1.jpg">
      <div class="input_video">

      <iframe
        width="900px"
        height="700px"
        frameborder="0"
        src="https://v.qq.com/txp/iframe/player.html?vid=k3235k6lcqa"
        allowFullScreen="true">

      </iframe>
    </div>

