    left: 80px;
    border-radius: 0.8rem;
  }

  a:hover{
    text-decoration: underline;
    text-decoration-color:#FF5783;
    height: 5px;
  }



  .mainTitle-text{
    text-align: left;
    margin-left: 150px;
    font-size: 35px;
    margin-top: 50px;
  }
  .mainTitle-image{
    border-radius: 10px;
    margin-left: 20px;
    width: 400px;
    height: 547px;
  }

  .mainContent{
    position:absolute;

    background:#FFEBF2;
    border-radius: 20px;
    width: 1200px;
    height: auto;
    left: 270px;
  }
  .full{
    height: auto;

  }

</style>



