import Vue from 'vue'
import Router from 'vue-router'
import mainpage from'../components/MainPage.vue'
import workpadgVis from'../components/workPageVis'
import newyear from'../components/workPageNewyear'
import eetoro from '../components/workPageEetoro'
import hangyue from'../components/workPageHangyuego'
import Acorner from '../components/workPageAcorner'
import kokowan from '../components/workPageKokowan'
import aboutme from '../components/aboutMePage'
import practice from'../components/practicePage'
import productDesign from '../components/productDesignPage'
import kitchef from'../components/productPageKitchef'
import fula from '../components/productPageFula'
import coffee from '../components/productPageCoffee'
import blender from '../components/workBlender'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/mainpage'
    },
    {
      path: '/mainpage',
      name: 'mainpage',
      component: mainpage
    },
    {
      path: '/workpageVis',
      name: 'workpadgVis',
      component: workpadgVis

    },

    {
      path: '/workpageNew',
      name: 'newyear',
      component: newyear

    },
    {
      path: '/blender',
      name: 'blender',
      component: blender

    },

    {
      path: '/eetoro',
      name: 'eetoro',
      component: eetoro

    },{

      path: '/Acorner',
      name: 'Acorner',
      component: Acorner

    }
    ,{

      path: '/HangYueGO',
      name: 'hangyue',
      component: hangyue

    },{

      path: '/KOKOWAN',
      name: 'kokowan',
      component: kokowan

    }
    ,{

      path: '/aboutme',
      name: 'aboutme',
      component: aboutme

    } ,{

      path: '/practice',
      name: 'practice',
      component: practice

    }
    ,{

      path: '/productDesign',
      name: 'productDesign',
      component: productDesign

    }  ,{

      path: '/kitchef',
      name: 'kitchef',
      component: kitchef

    },{

      path: '/fula',
      name: 'fula',
      component: fula

    },{

      path: '/coffee',
      name: 'coffee',
      component: coffee

    }
  ]
})



