<template>
  <div class="full">

    <div  class="mainContent">
      <el-collapse-transition>
        <div v-show="show1" class="works-1" style="width: 460px" @click="goKitchef">
          <div class="work-index" >01</div>
          <img  class="works-1-image" src="../assets/kichaf_logo.png">
          <div class="work-1-title">2020.4</div>
          <div style="text-align: left">
            <a class="work-1-link" onclick="">KITCHEF测距定量调料机 </a>
          </div>
          <div class="work-1-content" ></div>
        </div>
      </el-collapse-transition>
      <el-collapse-transition>
        <div v-show="show1" class="works-2" style="width: 400px" @click="gofula">
          <div class="work-index" >02</div>
          <img  class="works-2-image" src="../assets/fula_logo.png">
          <div class="work-1-title">2020.11</div>
          <div style="text-align: left">
            <a class="work-1-link" >FULA母婴消毒杯 </a>
          </div>
          <div class="work-1-content" ></div>
        </div>
      </el-collapse-transition>
      <el-collapse-transition>
        <div v-show="show1" class="works-3" style="width: 400px" @click="gocoffee">
          <div class="work-index" >03</div>
          <img  class="works-3-image" src="../assets/coffe_logo.png">
          <div class="work-1-title">2020.8</div>
          <div style="text-align: left">
            <a class="work-1-link" > 咖啡机模型制作</a>
          </div>
          <div class="work-1-content" ></div>
        </div>
      </el-collapse-transition>



    </div>

    <div  class="xuanfu" id="moveDiv">
      <div class="menu-item"  @click="gohome">Home</div>
      <div class="menu-item" @click="goAboutMe">About me</div>
      <div class="menu-item" style="color: #EF173E">Product Design</div>
      <div class="menu-item" @click="goPractice">Practice</div>
    </div>


  </div>
</template>

<script>


  export default {
    mounted() {
      this.show1=true
      document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')

    },

    data() {
      return {
        show1:false,
        flags: false,
        position: { x: 0, y: 0 },
        nx: '', ny: '', dx: '', dy: '', xPum: '', yPum: '',
      }
    },

    methods: {
      gocoffee(){
        this.$router.push('/coffee')


      },
      gohome(){
        this.$router.push('/mainpage')

      },
      goPractice(){
        this.$router.push('/practice')
      },
      goAboutMe(){
        this.$router.push('/aboutme')
      },

      goKitchef(){
        this.$router.push('/kitchef')

      },
      gofula(){
        this.$router.push('/fula')
      },

      // 实现移动端拖拽
      down(){
        this.flags = true;
        var touch;
        if(event.touches){
          touch = event.touches[0];
        }else {
          touch = event;
        }
        this.position.x = touch.clientX;
        this.position.y = touch.clientY;
        this.dx = moveDiv.offsetLeft;
        this.dy = moveDiv.offsetTop;
      },
      move(){
        if(this.flags){
          var touch ;
          if(event.touches){
            touch = event.touches[0];
          }else {
            touch = event;
          }
          this.nx = touch.clientX - this.position.x;
          this.ny = touch.clientY - this.position.y;
          this.xPum = this.dx+this.nx;
          this.yPum = this.dy+this.ny;
          moveDiv.style.left = this.xPum+"px";
          moveDiv.style.top = this.yPum +"px";
          //阻止页面的滑动默认事件；如果碰到滑动问题，1.2 请注意是否获取到 touchmove
          document.addEventListener("touchmove",function(){
            event.preventDefault();
          },false);
        }
      },
//鼠标释放时候的函数
      end(){
        this.flags = false;
      },
    }


  }
</script>

<style scoped>


  .menu-item{
    cursor: pointer;
    font-family: AbhayaLibre;
    margin-top: 30px;
    text-align: right;
    font-size: 30px;
    color: black;
  }
  .xuanfu {

    height: 4.5rem;
    width: 15rem;
    /* 如果碰到滑动问题，1.3 请检查 z-index。z-index需比web大一级*/
    z-index: 999;
    position: fixed;
    top: 374px;
    left: 80px;
    border-radius: 0.8rem;
  }

  .works-3{
    cursor: pointer;
    float: left;
    margin-top: 40px;
    margin-left: 10%;

  }
  .works-2{
    cursor: pointer;
    float:right;
    margin-right: 15%;
    margin-top: 300px;
  }
  .work-1-content{
    font-family: AbhayaLibre;

    margin-top: 15px;
    margin-bottom: 15px;
    text-align:left;

    font-size:20px;
  }
  a:hover{
    text-decoration: underline;
    text-decoration-color:#FF5783;
    height: 5px;
  }

  .work-1-link{
    font-family: AbhayaLibre;

    cursor:pointer;
    text-align:left;
    font-size:40px;
    color: black;

  }
  .work-1-title{
    font-family: Consolas;

    font-size:20px;
    margin-top: 15px;
    margin-bottom: 15px;
    text-align:left;
    color: #FF5783;
  }
  .work-index{
    text-align:left;
    font-size: 40px;
    color: #FF5783;
    font-family: Consolas;
  }
  .works-3-image{
    width: 400px;
    height: 500px;
    border-radius: 10px;
  }
  .works-2-image{
    width: 500px;
    height: 400px;
    border-radius: 10px;
  }
  .works-1-image{
    width: 400px;
    height: 300px;
    border-radius:  10px;
  }

  .works-1{

    cursor: pointer;
    float: left;
    margin-top: 50px;
    margin-left: 10%;
  }

  .mainContent{

    position:absolute;

    background:#FFEBF2;
    border-radius: 20px;
    width: 1200px;
    height: auto;

    left: 270px;
  }
  .full{
    height: auto;

  }

</style>



