<template>
  <div class="full">
    <div  class="mainTitle">





      <div class="mainTitle-text">"新春"DIY 立体书</div>



    </div>
    <div  class="mainContent">
      <div class="input_video">

        <iframe src="https://player.bilibili.com/player.html?aid=713695341&cid=282915577&page=1&danmaku=0&high_quality=1"
                allowfullscreen="allowfullscreen"
                width="100%"
                height="800"
                scrolling="no"
                frameborder="0"
                sandbox="allow-top-navigation allow-same-origin allow-forms allow-scripts"></iframe>



      </div>


      <div class="imageview">
        <img class="imageItem" src="../assets/newyear_0.png">
        <img class="imageItem" src="../assets/newyear_1.png">
      </div>
    </div>


    <div  class="xuanfu" id="moveDiv">
      <div class="menu-item" @click="backhome">Home</div>
      <div class="menu-item"@click="goAboutMe">About me</div>
      <div class="menu-item"@click="goProduct">Product Design</div>
      <div class="menu-item"@click="goPractice">Practice</div>
    </div>
  </div>
</template>

<script>

  export default {

    mounted() {
      document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')
    },

    data() {
      return {


        flags: false,
        position: { x: 0, y: 0 },
        nx: '', ny: '', dx: '', dy: '', xPum: '', yPum: '',
      }
    },

    methods: {
      goProduct(){
        this.$router.push('/productDesign')

      },
      goPractice(){
        this.$router.push('/practice')
      },
      goAboutMe(){
        this.$router.push('/aboutme')
      },
      backhome(){

        this.$router.push('/mainpage')

      },

    }


  }
</script>

<style scoped>
  .input_video{
    width: 1000px;
    height: 500px;
    margin-left: 8%;
    margin-top: 50px;
  }
  .imageItem{
    width: 950px;

  }
  .imageview{
    margin-top: 400px;
    width: 800px;
    margin-left:9%;
  }

  .menu-item{
    cursor: pointer;
    font-family: AbhayaLibre;
    margin-top: 30px;
    text-align: right;
    font-size: 30px;
    color: black;
  }
  .xuanfu {

    height: 4.5rem;
    width: 15rem;
    /* 如果碰到滑动问题，1.3 请检查 z-index。z-index需比web大一级*/
    z-index: 999;
    position: fixed;
    top: 374px;
    left: 80px;
    border-radius: 0.8rem;
  }

  a:hover{
    text-decoration: underline;
    text-decoration-color:#FF5783;
    height: 5px;
  }

  .mainTitle{
    margin-top: 50px;
    position:absolute;

    z-index:100;
    display:flex;

    flex-direction:row;

    align-items:center;

    margin-bottom: 10px;
    float: left;
    margin-left: 35%;
  }
  .mainTitle-text{

    text-align: left;
    font-size: 45px;

  }
  .mainTitle-image{
    border-radius: 10px;
    margin-left: 20px;
    width: 400px;
    height: 547px;
  }
  .mainContent{
    margin-top: 200px;
    position:absolute;
    z-index:50;
    background:#FFEBF2;
    border-radius: 20px;
    width: 1200px;
    height: auto;
    left: 270px;
  }
  .full{
    height: auto;

  }

</style>



