<template>
  <div class="full">
    <div  class="mainTitle">


      <div class="mainTitle-text">Eetoro外卖车租借服务系统</div>




    </div>
    <div  class="mainContent">
      <div class="imageview">
        <img  class="imageSet" src="../assets/eetoro/01.png">
        <img class="imageSet" src="../assets/eetoro/02.jpg">
        <img class="imageSet"src="../assets/eetoro/03.png">
        <img  class="imageSet"src="../assets/eetoro/04.jpg">
        <img class="imageSet" src="../assets/eetoro/05.png">
        <img class="imageSet" src="../assets/eetoro/06.png">
        <img class="imageSet" src="../assets/eetoro/07.jpg">
        <img class="imageSet" src="../assets/eetoro/08.jpg">
        <img class="imageSet" src="../assets/eetoro/09.jpg">
        <img class="imageSet" src="../assets/eetoro/10.png">
      </div>


    </div>

    <div  class="xuanfu" id="moveDiv">
      <div class="menu-item" @click="backhome">Home</div>
      <div class="menu-item"@click="goAboutMe">About me</div>
      <div class="menu-item"@click="goProduct">Product Design</div>
      <div class="menu-item"@click="goPractice">Practice</div>

    </div>
  </div>
</template>

<script>

  export default {

    mounted() {
      document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')

    },

    data() {
      return {



        flags: false,
        position: { x: 0, y: 0 },
        nx: '', ny: '', dx: '', dy: '', xPum: '', yPum: '',
      }
    },

    methods: {
      goProduct(){
        this.$router.push('/productDesign')

      },
      goPractice(){
        this.$router.push('/practice')
      },
      goAboutMe(){
        this.$router.push('/aboutme')
      },
      backhome(){

        this.$router.push('/mainpage')

      },

    }


  }
</script>

<style scoped>
  .imageSet{
    width: 1000px;
  }
  .imageview{
    margin-top: 100px;
    width: 800px;
    margin-left: 10%;
  }


  .menu-item{
    cursor: pointer;
    font-family: AbhayaLibre;
    margin-top: 30px;
    text-align: right;
    font-size: 30px;
    color: black;
  }
  .xuanfu {

    height: 4.5rem;
    width: 15rem;
    /* 如果碰到滑动问题，1.3 请检查 z-index。z-index需比web大一级*/
    z-index: 999;
    position: fixed;
    top: 374px;
    left: 80px;
    border-radius: 0.8rem;
  }

  a:hover{
    text-decoration: underline;
    text-decoration-color:#FF5783;
    height: 5px;
  }


  .mainTitle{
    position:absolute;
    margin-top: 50px;
    z-index:100;
    display:flex;

    flex-direction:row;

    align-items:center;

    margin-bottom: 10px;
    float: left;
    margin-left: 35%;
  }
  .mainTitle-text{

    text-align: left;
    font-size: 45px;

  }
  .mainTitle-image{
    border-radius: 10px;
    margin-left: 20px;
    width: 400px;
    height: 547px;
  }
  .mainContent{
    margin-top: 200px;
    position:absolute;

    background:#FFEBF2;
    border-radius: 20px;
    width: 1200px;
    height: 36700px;
    left: 270px;
  }
  .full{
    height: auto;

  }

</style>



