<script>


  export default {
        name: "MainPage",
      mounted() {
          this.show1=true
        document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')

      },

    data() {
      return {
        show1:false,
        flags: false,
        position: { x: 0, y: 0 },
        nx: '', ny: '', dx: '', dy: '', xPum: '', yPum: '',
      }
    },

    methods: {
      goBlender(){

        this.$router.push('/blender')

      },
      goProduct(){
        this.$router.push('/productDesign')

      },
      goPractice(){
        this.$router.push('/practice')
      },
      goAboutMe(){
        this.$router.push('/aboutme')
      },
          gokokowan(){
            this.$router.push('/KOKOWAN')
          },
          goCorner(){
            this.$router.push('/Acorner')

          },
      goHangyue(){
        this.$router.push('/HangYueGO')

      },
          goeetoro(){
            this.$router.push('/eetoro')

          },
      goToNew(){
        this.$router.push('/workpageNew')

      },
      goToVis(){
        this.$router.push('/workpageVis')

      },

          backhome(){



          },
      // 实现移动端拖拽
      down(){
        this.flags = true;
        var touch;
        if(event.touches){
          touch = event.touches[0];
        }else {
          touch = event;
        }
        this.position.x = touch.clientX;
        this.position.y = touch.clientY;
        this.dx = moveDiv.offsetLeft;
        this.dy = moveDiv.offsetTop;
      },
      move(){
        if(this.flags){
          var touch ;
          if(event.touches){
            touch = event.touches[0];
          }else {
            touch = event;
          }
          this.nx = touch.clientX - this.position.x;
          this.ny = touch.clientY - this.position.y;
          this.xPum = this.dx+this.nx;
          this.yPum = this.dy+this.ny;
          moveDiv.style.left = this.xPum+"px";
          moveDiv.style.top = this.yPum +"px";
          //阻止页面的滑动默认事件；如果碰到滑动问题，1.2 请注意是否获取到 touchmove
          document.addEventListener("touchmove",function(){
            event.preventDefault();
          },false);
        }
      },
//鼠标释放时候的函数
      end(){
        this.flags = false;
      },
    }


    }
</script>

