<template>
  <div class="full">
    <div  class="mainTitle">


      <div class="mainTitle-text">杭越GO<br>——杭州、绍兴家乡土特产美食APP</div>




    </div>
    <div  class="mainContent">
      <div class="modao">
      <iframe
        width="1000px"
        height="1000px"

        src="https://modao.cc/app/9EzUswSK6iUdLTPkCsBijNby4ZkI0Kr/embed/v2"
        allowTransparency="false"
        frameborder="0"></iframe>

      </div>
    </div>


    <div  class="xuanfu" id="moveDiv">
      <div class="menu-item" @click="backhome">Home</div>
      <div class="menu-item"@click="goAboutMe">About me</div>
      <div class="menu-item"@click="goProduct">Product Design</div>
      <div class="menu-item"@click="goPractice">Practice</div>
    </div>
  </div>
</template>

<script>

  export default {

    mounted() {
      document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')

    },

    data() {
      return {



        flags: false,
        position: { x: 0, y: 0 },
        nx: '', ny: '', dx: '', dy: '', xPum: '', yPum: '',
      }
    },

    methods: {
      goProduct(){
        this.$router.push('/productDesign')

      },
      goPractice(){
        this.$router.push('/practice')
      },
      goAboutMe(){
        this.$router.push('/aboutme')
      },
      backhome(){

        this.$router.push('/mainpage')

      },

    }


  }
</script>

<style scoped>
  .modao{
    margin-top: 50px;
    margin-left: 10%;
  }

  .menu-item{
    cursor: pointer;
    font-family: AbhayaLibre;
    margin-top: 30px;
    text-align: right;
    font-size: 30px;
    color: black;
  }
  .xuanfu {

    height: 4.5rem;
    width: 15rem;
    /* 如果碰到滑动问题，1.3 请检查 z-index。z-index需比web大一级*/
    z-index: 999;
    position: fixed;
    top: 374px;
    left: 80px;
    border-radius: 0.8rem;
  }

  a:hover{
    text-decoration: underline;
    text-decoration-color:#FF5783;
    height: 5px;
  }


  .mainTitle{
    position:absolute;
    margin-top: 50px;
    z-index:100;
    display:flex;

    flex-direction:row;

    align-items:center;

    margin-bottom: 10px;
    float: left;
    margin-left: 35%;
  }
  .mainTitle-text{

    text-align: left;
    font-size: 45px;

  }

  .mainContent{
    margin-top: 200px;
    position:absolute;

    background:#FFEBF2;
    border-radius: 20px;
    width: 1200px;
    height: auto;
    left: 270px;
  }
  .full{
    height: auto;

  }

</style>



